<template>
	<section class="my-works">
		<h3 class="section-title container" content="Works">Works</h3>
		<div class="filter container">
			<button
				:class="['filter__current', { active: filterIsOpen }]"
				@click="filterIsOpen = !filterIsOpen"
				@blur="blurHandling"
			>
				{{ filter }}
			</button>
			<ul class="filter__list">
				<li
					:class="[
						'filter__item',
						{ 'filter__item--active': filter === '全部' },
					]"
					@click="changeFilter"
					tabindex="-1"
				>
					全部
				</li>
				<li
					:class="[
						'filter__item',
						{ 'filter__item--active': filter === '前端' },
					]"
					@click="changeFilter"
					tabindex="-1"
				>
					前端
				</li>
				<li
					:class="[
						'filter__item',
						{ 'filter__item--active': filter === '全端' },
					]"
					@click="changeFilter"
					tabindex="-1"
				>
					全端
				</li>
				<li
					:class="[
						'filter__item',
						{ 'filter__item--active': filter === '後端' },
					]"
					@click="changeFilter"
					tabindex="-1"
				>
					後端
				</li>
				<li
					:class="[
						'filter__item',
						{ 'filter__item--active': filter === '協作' },
					]"
					@click="changeFilter"
					tabindex="-1"
				>
					協作
				</li>
			</ul>
		</div>
		<div class="container--lg">
			<div class="work-instructions">
				可使用以下帳號登入查看：<br />
				Email: user1@example.com<br />
				Password: 12345678
			</div>
		</div>
		<transition name="fade" mode="out-in">
			<div class="work-wrapper container--lg" :key="filter">
				<WorkCard
					v-for="work in filteredWorks"
					:work="work"
					:key="work.title"
				/>
			</div>
		</transition>
	</section>
</template>
<style lang="scss" scoped>
@import '@/assets/scss/Works.scss';
.fade-enter-active,
.fade-leave-active {
	transition: opacity 0.3s ease-out;
}
.fade-enter,
.fade-leave-to {
	opacity: 0;
}
</style>
<script>
import { titleObserver } from '@/modules/intersectionObservers'
import WorkCard from '@/components/WorkCard.vue'
import { works } from '@/content/works.json'

export default {
	components: {
		WorkCard,
	},
	data() {
		return {
			works,
			filter: '全部',
			filterIsOpen: false,
		}
	},
	methods: {
		changeFilter: function ({ target: node }) {
			this.filter = node.innerText
			this.filterIsOpen = false
		},
		blurHandling: function (event) {
			if (event.relatedTarget.classList.contains('filter__item')) {
				this.changeFilter({ target: event.relatedTarget })
			}
			this.filterIsOpen = false
		},
	},
	computed: {
		filteredWorks() {
			return this.works.filter((work) => {
				if (this.filter === '全部') {
					return true
				}
				return work.categories.includes(this.filter)
			})
		},
	},
	mounted() {
		const title = this.$el.querySelector('.section-title')
		titleObserver.observe(title)
	},
}
</script>
