<template>
	<section class="container">
		<h3 class="section-title untriggered" content="Skills">Skills</h3>
		<div class="skills skills--frontend">
			<div class="skills__title">Frontend</div>
			<div class="skills__list">
				{{ skillContent.frontend }}
			</div>
		</div>
		<div class="skills skills--backend">
			<div class="skills__title">Backend</div>
			<div class="skills__list">
				{{ skillContent.backend }}
			</div>
		</div>
		<div class="skills skills--others">
			<div class="skills__title">Others</div>
			<div class="skills__list">
				{{ skillContent.others }}
			</div>
		</div>
	</section>
</template>

<style lang="scss" scoped>
@import '@/assets/scss/Skills.scss';
</style>

<script>
import { titleObserver, sectionObserver } from '@/modules/intersectionObservers'
import { skills } from '@/content/skills.json'
export default {
	data() {
		return {
			skills,
		}
	},
	computed: {
		skillContent() {
			const joinChar = ' / '
			return {
				frontend: this.skills.frontend.join(joinChar),
				backend: this.skills.backend.join(joinChar),
				others: this.skills.others.join(joinChar),
			}
		},
	},
	mounted() {
		const title = this.$el.querySelector('.section-title')
		titleObserver.observe(title)
		sectionObserver.observe(title)
	},
}
</script>
