<template>
	<div class="work-card">
		<div class="work-card__header">
			<img
				:src="work.image.mobile"
				alt=""
				class="work-card__header__img work-card__header__img--mobile"
			/>
			<img
				:src="work.image.desktop"
				alt=""
				class="work-card__header__img work-card__header__img--desktop"
			/>
			<a
				:href="work.website"
				:class="[
					'work-card__header__link',
					{ 'work-card__header__link--dark': work.image.theme === 'light' },
					{ 'work-card__header__link--light': work.image.theme === 'dark' },
				]"
				target="_blank"
			></a>
		</div>
		<div class="work-card__body">
			<div class="work-card__body__name">{{ work.title }}</div>
			<!--                 <div class="work-category">全端</div> -->
			<div class="work-card__body__description" @click="truncateRemove">
				{{ work.description }}
			</div>
			<div class="work-card__body__docs">
				<div
					class="work-card__body__docs__item"
					v-for="(item, index) in work.docs"
					:key="index"
				>
					<a
						:class="[
							'work-card__body__docs__item-link',
							{
								'work-card__body__docs__item-link--github':
									item.type === 'github',
								'work-card__body__docs__item-link--medium':
									item.type === 'medium',
								'work-card__body__docs__item-link--swagger':
									item.type === 'swagger',
							},
						]"
						:href="item.link"
						target="_blank"
					></a>
				</div>
			</div>
			<div class="work-card__body__hashtags" @click="truncateRemove">
				{{ work.tags }}
			</div>
		</div>
	</div>
</template>
<style lang="scss" scoped>
@import '@/assets/scss/Works.scss';
</style>
<script>
export default {
	name: 'WorkCard',
	props: {
		work: Object,
	},
	methods: {
		truncateRemove: function ({ target: node }) {
			node.classList.toggle('active')
		},
	},
}
</script>
