<template>
	<section class="container">
		<h3 class="section-title" content="Experience">Experience</h3>
		<div class="experience-wrapper untriggered">
			<ExperienceCard
				v-for="(education, index) in educations"
				:key="index"
				:style="{ '--index': index }"
				:experience="education"
			/>
		</div>
		<div class="experience-wrapper untriggered">
			<ExperienceCard
				v-for="(experience, index) in experiences"
				:key="index"
				:style="{ '--index': index }"
				:experience="experience"
			/>
		</div>
	</section>
</template>

<style lang="scss" scoped>
@import '@/assets/scss/Experience.scss';
</style>

<script>
import { titleObserver, sectionObserver } from '@/modules/intersectionObservers'
import ExperienceCard from '@/components/ExperienceCard'
import { education, experience } from '@/content/experience.json'

export default {
	components: {
		ExperienceCard,
	},
	data() {
		return {
			experiences: experience,
			educations: education,
		}
	},
	mounted() {
		const title = this.$el.querySelector('.section-title')
		const wrapper = this.$el.querySelectorAll('.experience-wrapper')
		titleObserver.observe(title)
		wrapper.forEach((wrapperItem) => sectionObserver.observe(wrapperItem))
	},
}
</script>
