<template>
	<div
		:class="[
			'experience-card',
			{ 'experience-card--education': experience.type === 'education' },
		]"
	>
		<div class="experience-card__body" v-if="experience.type === 'education'">
			<div class="experience-card__body__organization">
				{{ experience.organization }}
			</div>
			<div class="experience-card__body__duration">
				{{ experience.duration }}
			</div>
			<div class="experience-card__body__program">
				{{ experience.program }}
			</div>
			<div
				class="experience-card__body__description"
				v-if="experience.description"
				v-html="experience.description"
			></div>
			<div class="experience-card__body__details">
				<div
					class="experience-card__body__detail"
					v-for="(detail, index) in experience.details"
					:key="index"
				>
					- <span v-html="detail"></span>
				</div>
			</div>
		</div>
		<div class="experience-card__body" v-else>
			<div class="experience-card__body__job">{{ experience.job }}</div>
			<div class="experience-card__body__duration">
				{{ experience.duration }}
			</div>
			<div class="experience-card__body__company">
				{{ experience.company }}
			</div>
			<div
				class="experience-card__body__description"
				v-if="experience.description"
				v-html="experience.description"
			></div>
			<div class="experience-card__body__details">
				<div v-for="(detail, index) in experience.details" :key="index">
					- <span v-html="detail"></span>
				</div>
			</div>
			<div class="experience-card__body__tags">
				<template v-for="(tag, index) in experience.tags">
					<br v-if="tag === 'BREAK'" :key="index" />
					<span class="experience-card__body__tag" v-else :key="index">
						#{{ tag }}
					</span>
				</template>
			</div>
		</div>
	</div>
</template>

<style lang="scss" scoped>
@import '@/assets/scss/ExperienceCard.scss';
</style>

<script>
export default {
	name: 'ExperienceCard',
	props: {
		experience: Object,
	},
}
</script>
