<template>
	<nav class="nav">
		<input
			type="checkbox"
			name="nav__checkbox"
			id="nav__checkbox"
			class="nav__checkbox"
			@change="handleScrolling"
		/>
		<div class="nav__list-wrapper">
			<ul class="nav__list">
				<li class="nav__list__item" @click="uncheckNav">
					<a href="./#home">首頁</a>
				</li>
				<li class="nav__list__item" @click="uncheckNav">
					<a href="./#experience">學經歷</a>
				</li>
				<li class="nav__list__item" @click="uncheckNav">
					<a href="./#skills">技能</a>
				</li>
				<li class="nav__list__item" @click="uncheckNav">
					<a href="./#works">作品</a>
				</li>
			</ul>
		</div>
		<label for="nav__checkbox" class="nav__hamburger"></label>
	</nav>
</template>

<style lang="scss" scoped>
@import '@/assets/scss/Navbar.scss';
</style>

<script>
export default {
	methods: {
		uncheckNav() {
			document.getElementById('nav__checkbox').checked = false
			this.handleScrolling()
			return
		},
		// remove scrolling when fullscreen menu
		handleScrolling() {
			if (document.getElementById('nav__checkbox').checked) {
				document.querySelector('body').style.overflow = 'hidden'
				return
			}
			document.querySelector('body').style.overflow = 'auto'
		},
	},
}
</script>
